
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import './App.css';

import List from './components/list/list';

function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<List></List>}></Route>
      </Routes>
    </BrowserRouter>
  );
}

export default App;