import React from "react";
import { useDispatch, useSelector } from "react-redux";

import classes from './list.module.css';
import { v4 as uuidv4 } from 'uuid';
import { Button } from 'antd';
import SvgFile from "../svgFile/svgFile";

import { addCustomUser } from "../../store/listReducer";
import { delCustomData } from "../../store/listReducer";

import { editName } from "../../store/listReducer";

const List = () => {

  const dispatch = useDispatch();
  const users = useSelector(state => state.list.listArray);


  return (
    <div>
      <div style={{ backgroundColor: "linen", display: "flex", alignItems: "top", justifyContent: "center", paddingTop: "40px", paddingBottom: "40px" }}>
        <div className={classes.listWrapper}>
          {users.length ? users.map(el =>
            <div
              className={classes.item}
              key={uuidv4()} >
              <p className={classes.taskName} style={{ backgroundColor: "#DD9E00", marginRight: '12px' }}>name of task: <span style={{ backgroundColor: "#A8E000" }}>{el.name}</span> </p>
              <span className={classes.taskNameId}> id: {el.id} </span>
              <Button className={classes.taskBtn} style={{ backgroundColor: "#00EAF2", marginRight: "20px" }} type="primary" onClick={() => dispatch(editName({ name: prompt(), idOf: el.id }))} > Edit this task :D</Button>
              <Button className={classes.taskBtn} onClick={() => dispatch(delCustomData(el.id))} style={{ backgroundColor: "#EA4A00" }} type="primary"> Delete this task :D</Button>
            </div>
          )
            : <div style={{ color: 'blue', textTransform: 'uppercase' }}> here is nothing </div>
          }
          <Button className={classes.taskBtn} style={{ marginTop: "40px" }} onClick={() => dispatch(addCustomUser(prompt()))} type="primary">Add new task :D</Button>
        </div>
      </div>
      <SvgFile></SvgFile>
    </div>
  )
}

export default List;

